import React, { useEffect } from "react";
import "date-fns";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import DateFnsUtils from "@date-io/date-fns";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import { KeyboardDatePicker, MuiPickersUtilsProvider, } from "@material-ui/pickers";
import { Transaction } from "models/Transaction";
import { toCurrency } from "Utils/numberFormat";
import { DEFAULT_DATE_TIME_FORMAT } from "Utils/setting";
import TransactionApiClient from "api/TransactionApiClient";

import moment from "moment";

import * as styles from "./styles.module.scss";

const transactionApiClient = new TransactionApiClient();

const AdminPageContainer: React.FC = () => {
  const [selectedDate, setSelectedDate] = React.useState<Date>(new Date("2014-08-18T21:11:54"));
  const [selectedStatus, setStatus] = React.useState<string>("submitted");
  const [transactions, setTransactions] = React.useState<Array<Transaction>>([]);

  useEffect(() => {
    transactionApiClient
      .getAllTransactions(selectedStatus)
      .then(trans => setTransactions(trans));
  }, [selectedStatus]);
  const [open, setOpen] = React.useState(false);
  const handleDateChange = (date: Date) => {
    setSelectedDate(date);
  };

  const handleStatusChange = (status: string) => {
    setStatus(status);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const rejectTransaction = (id:string) => {
    transactionApiClient
      .rejectTransaction(id)
      .then(() => {
        handleClose();
      });
  };

  const handleClose = () => {
    setOpen(false);
  };


  return (
    <Box p={4}>
      <Grid container spacing={3}>
        <Grid item xs={4}>
          <Card className={styles.card}>
            <CardContent className={styles.cardContent}>
              <Box>
                <Typography variant="h5">
                  Total Deposit
                </Typography>
                <Typography>
                  2 Nghìn
                </Typography>
              </Box>
              <Box>
                <Typography>
                  Last charged
                </Typography>
                <Typography>
                  2020.05.31
                </Typography>
                <Typography>
                  12:34:33
                </Typography>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={4}>
          <Card className={styles.card}>
            <CardContent className={styles.cardContent}>
              <Box>
                <Typography variant="h5">
                  Total Paid
                </Typography>
                <Typography>
                  Nghìn rưới
                </Typography>
              </Box>
              <Box>
                <Typography>
                  Last Paid
                </Typography>
                <Typography>
                  2020.05.24
                </Typography>
                <Typography>
                  23:11:33
                </Typography>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={4}>
          <Card className={styles.card}>
            <CardContent className={styles.cardContent}>
              <Box>
                <Typography variant="h5">
                  Blance
                </Typography>
                <Typography>
                  Nghìn hai
                </Typography>
              </Box>
              <Box className={styles.btnContainer}>
                <Button variant="outlined" color="primary">
                  Withdraw
                </Button>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12}>
          <Box className={styles.tableAction}>
            <Box className={styles.btnGroup}>
              <ButtonGroup variant="text" color="primary" aria-label="text primary button group">
                <Button onClick={() => handleStatusChange("submitted")}>Pending</Button>
                <Button onClick={() => handleStatusChange("approved")}>Approve</Button>
                <Button onClick={() => handleStatusChange("rejected")}>Reject</Button>
              </ButtonGroup>
            </Box>
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <Grid container className={styles.dateContainer}>
                <KeyboardDatePicker
                  className={styles.startDate}
                  margin="normal"
                  id="date-picker-dialog"
                  label="From"
                  format="MM/dd/yyyy"
                  value={selectedDate}
                  onChange={(event: any) => handleDateChange(event)}
                  KeyboardButtonProps={{
                    "aria-label": "change date",
                  }}
                />
                <KeyboardDatePicker
                  margin="normal"
                  id="date-picker-dialog"
                  label="To"
                  format="MM/dd/yyyy"
                  value={selectedDate}
                  onChange={(event: any) => handleDateChange(event)}
                  KeyboardButtonProps={{
                    "aria-label": "change date",
                  }}
                />
              </Grid>
            </MuiPickersUtilsProvider>
          </Box>
          <TableContainer component={Paper}>
            <Table aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell align="left">Date</TableCell>
                  <TableCell align="left">Type</TableCell>
                  <TableCell align="left">Agency</TableCell>
                  <TableCell align="left">Request Amount</TableCell>
                  <TableCell align="left">Status</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {transactions.map((transaction) => (
                  <TableRow key={transaction._id}>
                    <TableCell align="left">{moment(transaction.updatedAt).format(DEFAULT_DATE_TIME_FORMAT)}</TableCell>
                    <TableCell align="left">{transaction.paymentMethod.name}</TableCell>
                    <TableCell align="left">{transaction.senderAccountInfo}</TableCell>
                    <TableCell align="left">{toCurrency(transaction.amount)}</TableCell>
                    <TableCell align="left">
                      <Button variant="contained" onClick={handleClickOpen} className={styles.btn} color="primary">
                        Paid
                      </Button>
                      <Button className={styles.btn} onClick={handleClickOpen} variant="contained" color="secondary">
                        Reject
                      </Button>
                      <Dialog
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description"
                      >
                        <DialogTitle id="alert-dialog-title">Are you sure?</DialogTitle>
                        <DialogActions>
                          <Button onClick={() => rejectTransaction(transaction._id)} color="primary">
                            Yes
                          </Button>
                          <Button onClick={handleClose} color="primary" autoFocus>
                            No
                          </Button>
                        </DialogActions>
                      </Dialog>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>
    </Box>
  );
};


export default AdminPageContainer;
