import React, { useEffect } from "react";
import CustomerPaymentContainer from "containers/CustomerPaymentContainer";
import TransactionApiClient from "api/TransactionApiClient";
import { Transaction } from "../../models/Transaction";

const transactionApiClient = new TransactionApiClient();

const CustomerPaymentPage: React.FunctionComponent = () => {
  const [transactions, setTransactions] = React.useState<Array<Transaction>>([]);
  
  useEffect(() => {
    transactionApiClient
      .getAllTransactions()
      .then(trans => setTransactions(trans));
  }, []);
  return <CustomerPaymentContainer transactions={transactions} />;
};

export default CustomerPaymentPage;